import { json } from "@remix-run/node";
import type { ActionFunction } from "@remix-run/node";
import { commitSession, getSession, sessionKey } from "~/session.server";
import { supabaseAdmin } from "~/supabase.server";

export const action: ActionFunction = async ({ request }) => {
  const session = await getSession(request.headers.get("Cookie"));
  const authSession = session.get(sessionKey);

  if (!authSession || !authSession.refresh_token) {
    // Message has no session
    return json({});
  }

  console.log(authSession.refresh_token);
  console.log(authSession.expires_at);

  const { data, error } = await supabaseAdmin.auth.api.refreshAccessToken(
    authSession.refresh_token
  );

  if (error) {
    return json(
      { message: error?.message },
      {
        status: error?.status,
      }
    );
  }

  session.set(sessionKey, { ...data });

  return json(
    {},
    {
      headers: {
        "Set-Cookie": await commitSession(session),
      },
    }
  );
};
